.about-container {
  width: 90%;
  max-width: 1000px;
  padding: 40px;
  margin: 20px auto;
  overflow-y: auto;
  max-height: calc(100vh - 100px);
  z-index: 2;
}

.about-header {
  text-align: center;
  margin-bottom: 40px;
  padding-bottom: 30px;
  border-bottom: 2px solid rgba(0, 212, 255, 0.3);
}

.about-header h1 {
  font-size: 3em;
  margin: 0 0 10px 0;
  color: #fff;
  background: linear-gradient(135deg, #00d4ff 0%, #8b5cf6 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.subtitle {
  font-size: 1.3em;
  color: #00d4ff;
  margin: 0;
  font-weight: 300;
  letter-spacing: 2px;
}

.about-content {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.about-section {
  background: rgba(26, 31, 58, 0.6);
  padding: 30px;
  border-radius: 15px;
  border-left: 4px solid #00d4ff;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.about-section h2 {
  font-size: 2em;
  margin: 0 0 20px 0;
  color: #00d4ff;
}

.about-section p {
  font-size: 1.1em;
  line-height: 1.8;
  color: #e0e0e0;
  margin: 0 0 15px 0;
}

.about-section p:last-child {
  margin-bottom: 0;
}

.project-card {
  background: rgba(139, 92, 246, 0.1);
  padding: 25px;
  border-radius: 10px;
  margin-bottom: 20px;
  border: 1px solid rgba(139, 92, 246, 0.3);
  transition: all 0.3s ease;
}

.project-card:hover {
  background: rgba(139, 92, 246, 0.15);
  border-color: rgba(139, 92, 246, 0.5);
  transform: translateX(5px);
  box-shadow: 0 4px 15px rgba(139, 92, 246, 0.2);
}

.project-card h3 {
  font-size: 1.5em;
  margin: 0 0 15px 0;
  color: #8b5cf6;
}

.project-card p {
  font-size: 1em;
  line-height: 1.7;
  color: #d0d0d0;
  margin: 0;
}

.back-button {
  background: linear-gradient(135deg, #00d4ff 0%, #8b5cf6 100%);
  color: white;
  padding: 15px 40px;
  font-size: 1.1em;
  font-weight: 600;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(0, 212, 255, 0.3);
  transition: all 0.3s ease;
  margin-top: 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.back-button:hover {
  box-shadow: 0 8px 25px rgba(0, 212, 255, 0.5);
  transform: translateY(-2px);
}

/* Scrollbar styling */
.about-container {
  scrollbar-width: thin;
  scrollbar-color: #00d4ff transparent;
}

.about-container::-webkit-scrollbar {
  width: 8px;
}

.about-container::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

.about-container::-webkit-scrollbar-thumb {
  background-color: #00d4ff;
  border-radius: 4px;
}

.about-container::-webkit-scrollbar-thumb:hover {
  background-color: #8b5cf6;
}

/* Responsive design */
@media (max-width: 768px) {
  .about-container {
    width: 95%;
    padding: 25px 20px;
    max-height: calc(100vh - 80px);
  }

  .about-header h1 {
    font-size: 2.2em;
  }

  .subtitle {
    font-size: 1.1em;
  }

  .about-section {
    padding: 20px;
  }

  .about-section h2 {
    font-size: 1.6em;
  }

  .about-section p {
    font-size: 1em;
  }

  .project-card {
    padding: 20px;
  }

  .project-card h3 {
    font-size: 1.3em;
  }

  .back-button {
    padding: 12px 30px;
    font-size: 1em;
  }
}

@media (max-width: 480px) {
  .about-container {
    width: 98%;
    padding: 20px 15px;
    max-height: calc(100vh - 60px);
  }

  .about-header h1 {
    font-size: 1.8em;
  }

  .subtitle {
    font-size: 1em;
  }

  .about-section {
    padding: 15px;
  }

  .about-section h2 {
    font-size: 1.4em;
  }

  .about-section p {
    font-size: 0.95em;
  }

  .project-card {
    padding: 15px;
  }

  .project-card h3 {
    font-size: 1.2em;
  }

  .back-button {
    padding: 10px 25px;
    font-size: 0.9em;
  }
}
